import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Container, Paper } from "@material-ui/core";
import { NewPostForm } from "../../components/molecules/Form";
import { useSelector, useDispatch } from "react-redux";
import { selectUser } from "../../redux/reducers/authSlice";
import { getSinglePost, updatePost } from "../../redux/reducers/postSlice";
import { useHistory, useParams } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  root: {
    maxHeight: "85vh",
    marginTop: theme.spacing(5),
    padding: theme.spacing(3),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  form: {
    width: "100%",
    textAlign: "center",
  },
  paper: {
    width: "100%",
    padding: theme.spacing(2),
    height: "100%",
  },
}));

export default function EditPost() {
  const { id } = useParams();
  const classes = useStyles();
  const [file, setFile] = useState("");
  const [image, setImage] = useState("");
  const [content, setContent] = useState("");
  const dispatch = useDispatch();
  const history = useHistory();
  const userId = useSelector(selectUser).user.id;

  useEffect(() => {
    dispatch(getSinglePost(id)).then((res) => {
      if (res.success === true) {
        setContent(res.post.content);
        setImage(res.post.image);
      }
    });
  }, [dispatch, id]);

  const handleContentChange = (e) => {
    setContent(e.target.value);
  };

  const handleImageChange = (e) => {
    let url = URL.createObjectURL(e.target.files[0]);
    setFile(e.target.files[0]);
    setImage(url);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    dispatch(updatePost(content, file, userId, id)).then((res) => {
      if (res && res.success === true) {
        history.push(`/account/${userId}`);
      }
    });
  };

  return (
    <Container component="main" maxWidth="md" className={classes.root}>
      <Paper elevation={1} className={classes.paper}>
        <form
          onSubmit={handleSubmit}
          className={classes.form}
          encType="multipart/form-data"
        >
          <NewPostForm
            content={content}
            image={image}
            userId={userId}
            handleContentChange={handleContentChange}
            handleImageChange={handleImageChange}
          />
        </form>
      </Paper>
    </Container>
  );
}
